.slider-container{
    width: 20vw;
    height: 10vw;
    margin-left: 2vw;
    margin-top: -1vw;
}
.project_img{
    width: 100%;
    height: 10vw;
    border-radius: 0.75vw;
    margin-top: 0.4vw;
}
/* Next & previous buttons */
.prev{
    cursor: pointer;
    color: white;
    font-weight: bold;
    font-size: 1vw;
    position: absolute;
    top: 42.5%;
    left: 1vw;
  }
.next{
    cursor: pointer;
    color: white;
    font-weight: bold;
    font-size: 1vw;
    margin-left: 10vw;
    position: absolute;
    top: 42.5%;
    left: 12.5vw;
  }
  .prev:hover{
    color: orange;
  }
  .next:hover{
    color: orange;
  }

.image-slider {
   position: relative;
   height: 400px;
   display: flex;
   justify-content: center;
   align-items: center;
}
.image {
   width: 450px;
   height: 300px;
   border-radius: 12px;
}
  .currentSlide {
    transition-duration: 0.5s ease;
    opacity: 0;
 }
 .currentSlide.active {
    opacity: 1;
    transition-duration: 0.5s;
    transform: scale(1.2);
 }
 h2 {text-align: center;} 