.outer_body{
    width: 100%;
    height: 3.25vw;
    background-color: #ffffff00;
    position: fixed;
    z-index: 999;
    transition: background-color 0.5s;
}
.inner_box{
    width: 40vw;
    height: 3.5vw;
    background-color: rgba(128, 128, 128, 0);
    margin-left: 4vw;
    display: flex;
    align-items: center;
    transition: height 0.25;
}

.inner_box h2{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 1.4vw;
    color: #fe5b02;
    margin-top: 0.99vw;
}
.btn{
    width: 4vw;
    height: 2vw;
    background-color: #fe5a0200;
    font-family: 'Josefin Sans', sans-serif;
    border:none;
    font-size: 1.2vw;
}
.btn_home{
    margin-left: 1.75vw;
    cursor: pointer;
}
.btn_Contact{
    margin-left: 1vw;
    cursor: pointer;
}
.btn_Project{
    margin-left: 1vw;
    cursor: pointer;
}
.btn_About{
    margin-left: 1vw;
    cursor: pointer;
}
.hr_line{
    width: 2vw;
    height: 0.1vw;
    background-color: rgb(95, 95, 95);
    position: absolute;
    top: 1.5vw;
    left: 8.79vw;
    transform: rotate(90deg);
}
.hr_line2{
    width: 2vw;
    height: 0.1vw;
    background-color: rgb(95, 95, 95);
    position: absolute;
    top: 1.5vw;
    left: 30vw;
    transform: rotate(90deg);
}
header.active{
    background-color:#ffffffd7;
    box-shadow: 0 3px 1rem rgb(0,0,0,0.1);
}

.header.active .hr_line,
.header.active .hr_line2{
    color: rgb(58, 58, 58);
}
